import React from 'react';
import { FiPlus } from 'react-icons/fi';

import { Link } from 'react-router-dom';

import Header from './Header/index';

import boticarioImg from '../../assets/boticario.jpeg';
import havaianasImg from '../../assets/havaianas.jpeg';
import doritosImg from '../../assets/doritos.jpeg';
import bebeImg from '../../assets/bebe.jpeg';
import rainbowImg from '../../assets/rainbow.jpeg';

import './style.scss';

class Home extends React.Component {

  handleImageLoaded(element) {
    let img = element.target;
    let alturaImg = (img.offsetHeight / 2) - 78;
    let description = img.previousElementSibling;
    description.setAttribute('style', 'top:' + alturaImg + 'px')
  }

  showDescription(element) {
    let currentTarget = element.currentTarget;
    let img = currentTarget.querySelector('img')
    let description = img.previousElementSibling;
    img.classList.add('zoom')
    description.classList.add('show')
  }

  hideDescription(element) {
    let currentTarget = element.currentTarget;
    let img = currentTarget.querySelector('img')
    let description = img.previousElementSibling;
    img.classList.remove('zoom')
    description.classList.remove('show')
  }

  updateSize() {
    let campanhas = document.querySelectorAll('.campanha')
    // console.log(campanhas)
    campanhas.forEach(element => {
      let img = element.querySelector('img')
      let alturaImg = (img.offsetHeight / 2) - 78;
      let description = img.previousElementSibling;
      description.setAttribute('style', 'top:' + alturaImg + 'px')
      // console.dir(img)
    });
  }

  componentDidMount() {
    window.addEventListener('resize', this.updateSize);
  }

  render() {
    return (
      <div className="home" >
        <div className="container">
        <Header />
          <div className="campanha" onMouseOver={this.showDescription.bind(this)} onMouseOut={this.hideDescription.bind(this)}>
            <Link to="/oboticario">
              <div className="description">
                <FiPlus color="#fff" />
                <h3>OBOTICÁRIO</h3>
                <p>INTENSE CARNAVAL</p>
              </div>
              <img src={boticarioImg} onLoad={this.handleImageLoaded.bind(this)} alt="Campanha oBoticário - Intense Carnaval" />
            </Link>
          </div>
          <div className="campanha" onMouseOver={this.showDescription.bind(this)} onMouseOut={this.hideDescription.bind(this)}>
            <Link to="/havaianas">
              <div className="description">
                <FiPlus color="#fff" />
                <h3>HAVAIANAS</h3>
                <p>DIA DAS BRUXAS</p>
              </div>
              <img src={havaianasImg} onLoad={this.handleImageLoaded.bind(this)} alt="Campanha Havaianas - Dia das Bruxas" />
            </Link>
          </div>
          <div className="campanha" onMouseOver={this.showDescription.bind(this)} onMouseOut={this.hideDescription.bind(this)}>
            <Link to="/doritos">
              <div className="description">
                <FiPlus color="#fff" />
                <h3>DORITOS</h3>
                <p>TRIGONOMETRIA</p>
              </div>
              <img src={doritosImg} onLoad={this.handleImageLoaded.bind(this)} alt="Campanha Doritos - Trigonometria" />
            </Link>
          </div>
          <div className="campanha" onMouseOver={this.showDescription.bind(this)} onMouseOut={this.hideDescription.bind(this)}>
            <Link to="/bebe">
              <div className="description">
                <FiPlus color="#fff" />
                <h3>BEBE</h3>
                <p>MÉDICOS</p>
              </div>
              <img src={bebeImg} onLoad={this.handleImageLoaded.bind(this)} alt="Campanha Bebe - Médicos" />
            </Link>
          </div>
          <div className="campanha" onMouseOver={this.showDescription.bind(this)} onMouseOut={this.hideDescription.bind(this)}>
            <Link to="/rainbow">
              <div className="description">
                <FiPlus color="#fff" />
                <h3>DORITOS</h3>
                <p>ESCUTE A VOZ DE RAINBOW</p>
              </div>
              <img src={rainbowImg} onLoad={this.handleImageLoaded.bind(this)} alt="Campanha Doritos - Escute a voz de Rainbow" />
            </Link>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
