import React from 'react';
import { FiPlus } from 'react-icons/fi'

import { Link } from 'react-router-dom';

import './style.scss';

class Header extends React.Component {

  componentDidMount() {

  }
  render() {
    return (
      <header className="header">
        <div className="main">
          <h1 className="hi">HI! I'M</h1>
          <div className='name'>
            <h1 className="first-name">FERNANDO</h1>
            <div className="last-name-about">
              <h1 className="last-name">FREGATE</h1>
              <div className="about">
                <Link to="/about-me" className="me">
                  <span className="icon"><FiPlus color="#323232" /></span>
                  <span className="text">ABOUT ME</span>
                </Link>
              </div>
            </div>
          </div>
          <div className="description-box">
            <h2 className="description-one">
              A creative <br />
              art director <br />
              based in <br />
              sao paulo.</h2>
            <h2 className="description-two">
              I hope you <br />
              enjoy my work.
            </h2>
          </div>
        </div>

      </header>
    );
  }
}

export default Header;